<template>
  <div>
    <div class="title">
        权限管理
    </div>
    <!-- 权限管理主页面主题内容 -->
    <div class="cntent">
      <h5>操作</h5>
      <el-divider></el-divider> 
      <div class="listBox">
        <!-- 每一项的start -->
        <div @click="FnPFrole()">
          <svg class="icon icBlue default">
            <use xlink:href="#icon-quanxian"></use>
          </svg>
          项目权限
        </div>
        <!-- 每一项的end -->
        <div @click="FnPFsystem()">
          <svg class="icon icHuang default">
            <use xlink:href="#icon-21"></use>
          </svg>
          系统权限
        </div>
      </div>
      <h5>数据</h5>
      <el-divider></el-divider> 
      <div class="listBox">
        <!-- 每一项的start -->
        <div @click="Fnbinding()">
          <svg class="icon icZi default">
            <use xlink:href="#icon-cedaohang-quanxian"></use>
          </svg>
          设备分配
        </div>
        <!-- 每一项的end -->
      </div>
    </div>
  </div>
</template>

<script>
export default {
  data(){
    return{

    };
  },
  methods:{
    FnPFrole(){//项目权限点击事件
      console.log('-----项目权限点击')
      this.$router.push("/index/PFrole")
    },
    Fnbinding(){//设备分配点击事件
      this.$router.push('/index/binding')
    },
    FnPFsystem(){//系统权限点击事件
      this.$router.push('/index/PFsystem')
    }
  }
}
</script>

<style lang="stylus" scoped>
.title
    font-size 20px
    color #333
    margin-bottom 20px
    margin-top 20px
    padding-left 15px
    border-left 3px solid #409EFF
.cntent
  padding 25px
  h5 
    font-size 24px
    margin-top 15px
  .listBox
    overflow hidden
    div 
      text-align center
      line-height 40px
      font-size 18px
      width 97px
      color #333
      margin 0 22px
      float left
      .default
        height 40px
        width 40px
        padding 26px
        color #fff
        display block
      .icBlue
        background #1b9af7
      .icHuang 
        background #feae1b
      .icZi
        background #7b72e9
</style>


